const info = $('#info');
    info.tooltip({
    animation: false,
    trigger: 'manual'
});

function CreateVectorSource(layerName) { 
  const vs= new VectorSource({
    projection: "EPSG:3857",
    format: new GeoJSON(),
    loader: function (extent) {
      var ext = proj.transformExtent(
        extent,
        proj.get("EPSG:3857"),
        proj.get("EPSG:4326")
      );
     var url = 'geojson.php?layer=' + layerName +
         '&bbox=' + ext.join(',');
     var xhr = new XMLHttpRequest();
     xhr.open('GET', url);
     var onError = function() {
       this.removeLoadedExtent(extent);
     }
     xhr.onerror = onError;
     xhr.onload = function() {
       if (xhr.status == 200) {
         vs.addFeatures(
             vs.getFormat().readFeatures(xhr.responseText, {
                    dataProjection: "EPSG:4326",
                    featureProjection: "EPSG:3857"
                }));
       }
     }
     xhr.send();
   },
   strategy: loadingstrategy.bbox
 })
 return(vs);
 }
function createVectorLayer(layerName) {
  // export
  const vs = CreateVectorSource(layerName);
    if (layerName=="fynd") {
        var st=createPointStyle("196,60,457");
        var maxResolution=0.388657133911758;
    }
    else if (layerName=="elevation2m") {
        var st=createLineStyle("216,109,51");
        var maxResolution=9.554628535647032;
    }
    else {
        var st=createPolyStyle("232,113,141");
        var maxResolution=9.554628535647032;
    }
    return new VectorLayer({
        title: layerName,
        source: vs,
        style: st,
        minResolution: 0.0121455354347424,
        maxResolution: maxResolution,
  });
}

