function createPolyStyle(color) {
  // export
  return [
    new Style({
        stroke: new Stroke({
            color: 'rgba(40,40,40, 0.8)',
            width: 1
          }),
          fill: new Fill({
            color: 'rgba('+color+', 1.0)'
          }) 
    }),
  ];
}
function createLineStyle(color) {
  // export
  return [
    new Style({
        stroke: new Stroke({
            color: 'rgba('+color+', 0.8)',
            width: 1
          }) 
    }),
  ];
}

function setCenter(lon,lat,zoom) {
    var curzoom=map.getView().getZoom();
    map.getView().setCenter(proj.transform([lon, lat], 'EPSG:4326', 'EPSG:3857'));
    if (curzoom < zoom)
        map.getView().setZoom(zoom);
}
function showbyid(tb,id,fid=0,center=false) {
    // Visa med ID
    $.getJSON( "getdata.php", {
        tb: tb,
        id: id,
        fid: fid,
    })
    .done(function( data ) {
        var buffer="<table>";
        var lon,lat;
        // buffer+="<table>";
        $.each( data.features, function(i,feature ) {
            $.each(feature, function(j,values) {
                if (values[1]=="geometry") {
                    var coord = values[3].split(" ");
                    lon=coord[0];
                    lat=coord[1];
                    buffer+="<tr><td>"+values[0]+"</td><td>"+values[1]+"</td><td>"+values[2]+": "+coord[0]+" "+coord[1]+"</td></tr>";
                }
                else {
                    if (values[0]=="fyndnummer" || values[0]=="intrasisid")
                        buffer+="<tr><td>"+values[0]+"</td><td>"+values[1]+"</td><td><a target='_blank' href='https://uppakra.dh.gu.se/finds/image.html?id="+values[3]+"'>"+values[3]+"</a></td></tr>";
                    else
                        buffer+="<tr><td>"+values[0]+"</td><td>"+values[1]+"</td><td>"+values[3]+"</td></tr>";
                }  
            });
        });
        buffer+="</table>";
               
        $("#datatable").html(buffer);
        if (center)
            setCenter(lon,lat,18);     
    });    
}
const displayFeatureInfo = function(pixel) {
    // on mouse over event
    info.css({
        left: pixel[0] + 'px',
        top: (pixel[1] - 15) + 'px'
    });
    var obj = map.forEachFeatureAtPixel(pixel, 
        function(feature,layer) {
            return [feature,layer];
        });
        if (obj) {
            map.getTargetElement().style.cursor = 'pointer';
            var feature=obj[0];
            var layer=obj[1];
            if (layer==fynd) {
                var title=feature.get('id')+". "+feature.get('name');
            }
            else if (layer==elevation2m) {
                info.tooltip('hide')
                .attr('data-original-title', '');
                map.getTargetElement().style.cursor = 'crosshair';
            }
            else {
                var title=feature.get('id')+". "+feature.get('name');
            }
            // .tooltip('fixTitle')
            info.tooltip('hide')
                .attr('data-original-title', title)
                .tooltip('show');
        } else {
          info.tooltip('hide');
          map.getTargetElement().style.cursor = 'crosshair';
        }
      };
      
    const handleFeatureInfo = function(pixel,coordinate) {
        // on click event
        var obj = map.forEachFeatureAtPixel(pixel, function(feature,layer) {
            return [feature,layer];
        });
        if (obj) {  
            var feat=obj[0];
            var layer=obj[1];
            if (layer==fynd) {
                var id=feat.get('id');
                showbyid('fynd',id);
            }
            else if (layer==elevation2m) {
                ;
            }
            else {
                var id=feat.get('id');
                showbyid('arkeologiskt',id);
            }  
        }
        else {
            // Default: visar koordinater
            var coord=proj.toLonLat(coordinate);
            $("#geometry").val(coord[0]+" "+coord[1]); 
            
  
        }   
    } 

// Create Layers
const tiles=createTileLayer("topowebb_nedtonad");
const elevation2m=createVectorLayer("elevation2m");
const vector2=createVectorLayer("arkeologiskt");
const fynd=createVectorLayer("fynd");
const map = new Map({
  target: 'map',
  layers: [tiles,elevation2m,vector2,fynd],
  view: new View({
    center: [0, 0],
    zoom: 6
  })
});

/*
 [new TileLayer({
      source: new OSM(),
    })]
*/

map.on('pointermove', function(evt) {
    if (evt.dragging) {
        info.tooltip('hide');
        return;
    }
    displayFeatureInfo(map.getEventPixel(evt.originalEvent));
});

map.on('click', function(evt) {
    handleFeatureInfo(evt.pixel,evt.coordinate);
});
